<template>
    <list-model :category="category"></list-model>
</template>
<script>
import listModel from '@/components/ListModel.vue'
export default {
    name: 'React',
    components: {
        listModel
    },
    data() {
        return {
            category: 'React'
        }
    }
}
</script>



